import React, { useEffect, useState } from "react";
import itemData from "./newItemData.json";
import { initializeApp } from "firebase/app";
import {
  getFirestore,
  getDocs,
  collection,
  addDoc,
} from "firebase/firestore/lite";
import {
  getAuth,
  signInWithEmailAndPassword,
  onAuthStateChanged,
  signOut,
} from "firebase/auth";

const firebaseConfig = {
  apiKey: "AIzaSyAJFb_WYit0aL7UVhej1S2n2u0MKr9azyc",
  authDomain: "ccp-site-5fc0f.firebaseapp.com",
  projectId: "ccp-site-5fc0f",
  storageBucket: "ccp-site-5fc0f.appspot.com",
  messagingSenderId: "711821240422",
  appId: "1:711821240422:web:76c26932336439f22825a7",
  measurementId: "G-3GHJNSBRRN",
};

const app = initializeApp(firebaseConfig);
const db = getFirestore(app);
const auth = getAuth();

const App = () => {
  const [data, setData] = useState([]);
  const [creds, setCreds] = useState({
    user: false,
    email: "",
    password: "",
    query: "",
  });

  useEffect(() => {
    onAuthStateChanged(auth, (user) => {
      if (user) {
        setCreds({ ...creds, user: user.uid });
      } else {
        setCreds({ ...creds, user: false });
      }
    });
  }, []);

  useEffect(() => {
    setData(
      itemData.filter(
        ({ Item, Item_Description }) =>
          Item.toLowerCase().includes(creds.query.toLowerCase()) ||
          Item_Description.toLowerCase().includes(creds.query.toLowerCase())
      )
    );
  }, [creds.query]);

  const handleSubmit = (e) => {
    e.preventDefault();
    const { email, password } = creds;

    signInWithEmailAndPassword(auth, email, password)
      .then(({ user }) => {
        setCreds({ ...creds, email: "", password: "", user: user.uid });
      })
      .catch((error) => {
        setCreds({ ...creds, email: "", password: "" });
        alert("Enter a valid Email or Password");
      });
  };

  const handleChange = (e) => {
    setCreds({ ...creds, [e.target.name]: e.target.value });
  };

  const handleSignOut = () => {
    signOut(auth)
      .then(() => setCreds({ ...creds, user: false }))
      .catch((error) => console.log(`Error: ${error}`));
  };

  return (
    <div
      className={`flex flex-col h-screen text-xxs md:text-base container mx-auto ${
        !creds.user && "justify-center max-w-md"
      }`}
    >
      {!creds.user ? (
        <form
          onSubmit={handleSubmit}
          className="bg-gray-100 shadow-md rounded px-8 pt-6 pb-8 mb-4 mx-5 text-sm"
        >
          <div className="mb-4">
            <label
              className="block text-grey-800 text-sm font-bold mb-2"
              htmlFor="username"
            >
              Email
            </label>
            <input
              autoComplete="off"
              className="shadow appearance-none border rounded w-full py-2 px-3 text-grey-800"
              name="email"
              type="email"
              placeholder="Email"
              value={creds.email}
              onChange={handleChange}
            />
          </div>
          <div className="mb-6">
            <label
              className="block text-grey-800 text-sm font-bold mb-2"
              htmlFor="password"
            >
              Password
            </label>
            <input
              autoComplete="off"
              className="shadow appearance-none border border-red rounded w-full py-2 px-3 text-grey-800 mb-3"
              name="password"
              type="password"
              placeholder="Password"
              value={creds.password}
              onChange={handleChange}
            />
          </div>
          <div className="flex items-center justify-between">
            <button
              className="bg-blue-500 hover:bg-blue-800 text-white font-bold py-2 px-4 rounded"
              type="submit"
            >
              Sign In
            </button>
          </div>
        </form>
      ) : (
        <div>
          <header className="flex flex-col">
            <div className="flex justify-center items-center flex-col my-5">
              <input
                autoComplete="off"
                type="search"
                name="query"
                value={creds.query}
                onChange={handleChange}
                placeholder="Search..."
                className="border-2 rounded pl-3 py-1"
              />
            </div>
            <div className="inline-flex justify-center items-center">
              <button
                onClick={handleSignOut}
                className="border bg-blue-500 text-white rounded-md px-2 py-1 mb-4"
              >
                Sign Out
              </button>
            </div>
            {/* <div className="inline-flex justify-center items-center">
              <button
                onClick={readAllItems}
                className="border bg-blue-500 text-white rounded-md px-2 py-1 mb-4"
              >
                Add Doc
              </button>
            </div> */}
            <div className="grid grid-cols-6 gap-3 items-center text-center font-bold border-b-2 mx-2">
              <div>Item Number</div>
              <div>Item Description</div>
              <div>Material Cost</div>
              <div>Labor Cost</div>
              <div>Overhead Cost</div>
              <div>Total Cost</div>
            </div>
          </header>
          <main className="flex-1 overflow-y-auto pt-4 px-2">
            {data
              .filter(({ Item_Type }) => Item_Type === "FIN GOOD")
              .map(
                ({
                  Item,
                  Item_Description,
                  materialCost,
                  laborCost,
                  overheadCost,
                }) => (
                  <div
                    key={Item}
                    className="grid grid-cols-6 gap-3 items-center text-center mb-4"
                  >
                    <div>{Item}</div>
                    <div>{Item_Description}</div>
                    <div>€{materialCost.toFixed(2)}</div>
                    <div>€{laborCost.toFixed(2)}</div>
                    <div>€{overheadCost.toFixed(2)}</div>
                    <div>
                      €{(materialCost + laborCost + overheadCost).toFixed(2)}
                    </div>
                  </div>
                )
              )}
          </main>
        </div>
      )}
    </div>
  );
};

export default App;
